




























import ChangePasswordModal from "@/components/ChangePasswordModal.vue";
import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Component, Vue} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";
@Component({
  components: {ChangePasswordModal},
})
export default class SiteHeader extends Vue {
  @Getter("permission/currentPermission") readonly currentPermission!: string;
  displayChangePasswordModal: boolean = false;
  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];

  redirect(name: string): void {
    this.$router.push({name});
  }

  goToTab(tabName: string): void {
    this.$router.push({query: {tab: tabName}});
    this.updateTab(tabName);
  }

  //TODO
  get userInfo(): Object {
    return this.currentPermission === "ROOT"
      ? {email: "root@gmail.com", name: "Root"}
      : this.currentPermission === "ADMIN"
      ? {email: "admin@gmail.com", name: "Admin"}
      : {email: "user@gmail.com", name: "User"};
  }

  //TODO
  get dropdownItems(): any[] {
    return this.currentPermission === "ROOT"
      ? []
      : this.currentPermission === "ADMIN"
      ? [
          {label: "My Profile", action: () => null},
          {
            label: "Change password",
            action: () => {
              this.displayChangePasswordModal = true;
            },
          },
        ]
      : [
          {label: "Plan & Setting", action: () => null},
          {
            label: "Change password",
            action: () => {
              this.displayChangePasswordModal = true;
            },
          },
        ];
  }
}
